html, body, #root {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

/* unvisited link */
a:link {
    color: green;
    text-decoration: none;
    font-weight: bold;
  }
  
  /* visited link */
  a:visited {
    color: green;
    text-decoration: none;
  }
  
  /* mouse over link */
  a:hover {
    color: green;
    text-decoration: underline;
    text-shadow: 1px salmon;
    font-weight: bold;
}
  
  /* selected link */
  a:active {
    color: green;
    text-decoration: none;
  }


  ul {
    list-style-type: none;

  }

